import { Formik } from 'formik';
import { clientAPI } from '../../service/axios.config';
import DayOfWeek from 'Components/DayOfWeek/DayOfWeek';
import {
  Wrapper,
  Dialog,
  KindStyle,
  SubBTN,
} from './SignUp.styled';
import { useEffect, useState } from 'react';

const SignUp = ({ Close, kind_trainee, day, time, date }) => {
  const [dayTranslate, setDayTranslate] = useState('');

  useEffect(() => {
    const DayOfWeekTranslate = () => {
      if (day === "monday") {
        return (setDayTranslate(''));
      };
      if (day === "tuesday") {
        return (setDayTranslate(''));
      };
      if (day === "wednesday") {
        return (setDayTranslate(''));
      };
      if (day === "thursday") {
        return (setDayTranslate(''));
      };
      if (day === "friday") {
        return (setDayTranslate(`'`));
      };
      if (day === "saturday") {
        return (setDayTranslate(''));
      };
      if (day === "sunday") {
        return (setDayTranslate(''));
      };
      return (null);
    };
    DayOfWeekTranslate();
  }, [day]);

  const CheckKindTrainee = () => {
    if (kind_trainee === "-") {
      return (
        <>
          <Dialog>
            <h3>,           .  ,   </h3>
            <button type="button" onClick={Close}>
              
            </button>
          </Dialog>
        </>
      )
    };

    
    return (
      <>
        <Dialog>
          <h4>  <KindStyle>{kind_trainee}  <DayOfWeek day={day} time={time} /></KindStyle></h4><br />
          <h4> ,   </h4><br />
          <Formik
            initialValues={{
              id: "",
              day_translate: dayTranslate,
              info: {
                date: date,
                day: day,
                time: time,
                kind_trainee: kind_trainee,
                name: "",
              }
            }}
            onSubmit={async values => {
              await clientAPI.sendDataUsers(values);
              // await new Promise(resolve => setTimeout(resolve, 500));
              // alert(JSON.stringify(values, null, 2));
            }}
          >
            {props => {
              const {
                values,
                isSubmitting,
                handleChange,
                handleBlur,
                handleSubmit,
              } = props;
              return (
                <form
                  onSubmit={handleSubmit}
                >
                  <label htmlFor="info.name">
                     '
                  </label><br/>
                  <input
                    id="info.name"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.value}
                  /><br/>
                  <label htmlFor="id">
                      
                  </label><br/>
                  <input
                    id="id"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.value}
                  /><br/>
                  <SubBTN type="button" onClick={Close}>
                    
                  </SubBTN>
                  <SubBTN type="submit" disabled={isSubmitting}>
                    
                  </SubBTN>
                </form>
              );
            }}
          </Formik>
        </Dialog>
      </>
    )
  }

  return (
    <Wrapper>
      <CheckKindTrainee />
    </Wrapper>
  )
};

export default SignUp;